import React, { useMemo, useState } from 'react';
import { Avatar, Box, Button, Container, TextField, Typography } from '@mui/material';

import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { LoginService } from '../../services';
import { useHistory } from 'react-router';
import { useSnackbar } from 'notistack';

export const Login = () => {
    const [codeState, setCodeState] = useState(new Date().valueOf());
    const [loginForm, setLoginForm] = useState({
        email: '',
        password: '',
        code: '',
    });
    const [errorForm, setErrorForm] = useState({
        email: false,
        password: false,
        code: false,
        summary: '',
    });
    const history = useHistory();
    const snakeBar = useSnackbar();

    function handleSubmit(e) {
        e.preventDefault();

        if (loginForm.email && loginForm.password && loginForm.code) {
            setErrorForm({
                email: false,
                code: false,
                password: false,
                summary: '',
            });
            LoginService.signIn(loginForm).subscribe({
                next: (resp) => {
                    if (resp.errcode === 200) {
                        history.push('/');
                    } else {
                        setErrorForm({
                            ...errorForm,
                            summary: resp.errmessage,
                        });
                    }
                },
                error: (err) => {
                    snakeBar.enqueueSnackbar('', {
                        variant: 'error',
                    });
                },
            });
        } else {
            const newState = {
                email: !loginForm.email,
                password: !loginForm.password,
                code: !loginForm.code,
            };
            setErrorForm({ ...errorForm, ...newState });
        }
    }

    function updatedFormValue(key) {
        return (e) => {
            setErrorForm({
                ...errorForm,
                [key]: !e.target?.value,
            });

            setLoginForm((state) => ({
                ...state,
                [key]: e.target.value,
            }));
        };
    }

    function refreshCode(): void {
        setCodeState(new Date().valueOf());
    }

    const codeUrl = useMemo(() => {
        return `/verify/code?state=${codeState}`;
    }, [codeState]);

    return (
        <Container maxWidth='xs'>
            <Box
                sx={{
                    marginTop: 8,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}>
                <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component='h1' variant='h5'>
                    
                </Typography>
                <Box component='form' onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
                    <TextField
                        margin='normal'
                        required
                        fullWidth
                        id='email'
                        label=''
                        name='email'
                        autoComplete='email'
                        autoFocus
                        value={loginForm.email}
                        onChange={updatedFormValue('email')}
                        error={errorForm.email}
                        helperText={errorForm.email && ''}
                    />
                    <TextField
                        margin='normal'
                        required
                        fullWidth
                        name='password'
                        label=''
                        type='password'
                        id='password'
                        autoComplete='current-password'
                        value={loginForm.password}
                        onChange={updatedFormValue('password')}
                        error={errorForm.password}
                        helperText={errorForm.password && ''}
                    />
                    <TextField
                        margin='normal'
                        required
                        fullWidth
                        name='code'
                        label=''
                        id='code'
                        value={loginForm.code}
                        onChange={updatedFormValue('code')}
                        InputProps={{
                            endAdornment: <img src={codeUrl} onClick={refreshCode} alt='' />,
                        }}
                        error={errorForm.code}
                        helperText={errorForm.code && ''}
                    />
                    {errorForm.summary && (
                        <Typography variant='overline' color={'red'}>
                            {errorForm.summary}
                        </Typography>
                    )}
                    <Button type='submit' fullWidth variant='contained' sx={{ mt: 3, mb: 2 }}>
                        
                    </Button>
                </Box>
            </Box>
        </Container>
    );
};
